import React from 'react';
import Title from '../components/Title';
import ImgText from '../components/ImgText';
import Slider from 'react-animated-slider';
import 'react-animated-slider/build/horizontal.css';
import TextBlock from './TextBlocks/Home.json';
const lang = window.localStorage["language"] || "en";

var slides = [];
for(let quote of TextBlock.quotes){
  slides.push({
    "description":quote[lang]
  })
}
console.log(slides)

class Quotes extends React.Component {
  render () {
    return (
      <Slider autoplay="3000" infinite="true">
  {slides.map((slide, index) => <div key={index}>
    <div>{slide.description}</div>
  </div>)}
</Slider>
    )
  }
}

export default class Home extends React.Component {
  render () {
    return (
      <main>
        <Title background='./imgs/backdrop-home.jpg'>
          <div>Parenting Across Borders</div>
          <i style={{fontSize:"0.6em"}}>With and For International Parents</i>
        </Title>
<div className="content">
  <h2>Quotes From Mothers</h2>
  <Quotes/>
</div>
      </main>
    )
  }
}